
import React, { useState } from 'react'
import './Login.css'
import img from '../../image/logo.png'
import { Link, useNavigate } from 'react-router-dom'
import * as Yup from "yup"
import { useFormik } from 'formik'
import axios from 'axios'

export default function Login({saveUserData}) {

  let baseUrl='https://ecommerce.routemisr.com'
   let [errorMassege,setErrorMassage] =useState("")
   let [loading,setLodaing] =useState(true)

  let valid = Yup.object({
    email:Yup.string().email("Email doesn't exist").required(),
    password:Yup.string().matches(/^[A-Z][a-z0-9!@#$$%^&*()_-]{3,16}$/,"Incorrect password").required(),
  })

let formik = useFormik({
  initialValues:{
    email:"",
    password:"",
  },
  onSubmit:(values)=>{
    sendLoginData(values)
  },
  validationSchema:valid
})

let navigate =useNavigate()

async function sendLoginData(values){
  setLodaing(false)
  let {data} = await axios.post(`${baseUrl}/api/v1/auth/signin`,values).catch((err)=>{
    setErrorMassage(err.response.data.message)
    setLodaing(true)
  })
  setLodaing(true)
  if(data.message =="success"){
    saveUserData(data.user)
    localStorage.setItem("token",data.token)
    navigate('/home')
   }
}




  return <>
    <div className="main">
      <div className="container p-lg-5">
        <div className="login card border-0 shadow-lg ">
          <div className="card-body p-0">
            <div className="row">
              <div className="col-lg-6 d-none d-lg-block leftPart"></div>
              <div className="col-lg-6 login">
                <div className="p-5">
                  <div className='text-center'>
                    <img src={img} className='mb-3' height="72" />
                    <h1 className='h4 text-muted mb-4'>Log in to GameOver</h1>
                  </div>
                  <form className='user' onSubmit={formik.handleSubmit}>
                    <div className='form-group'>
                      <input onChange={formik.handleChange} type='text' name='email' placeholder='Email' className='form-control py-2 userForm' />
                      {formik.errors.email? <p className='alert alert-danger py-0 mt-1'>{formik.errors.email}</p>:""}
                    </div>
                    <div className='form-group'>
                      <input onChange={formik.handleChange} type='password' name='password' placeholder='Password' className='form-control py-2 userForm' />
                      {formik.errors.password? <p className='alert alert-danger py-0 mt-1'>{formik.errors.password}</p>:""}
                    </div>
                    {loading?<button type='submit' name='submitButton' className='btn btn-primary w-100 py-2'>
                      <span>Login</span>
                    </button> : <div className='text-center'><i className="fa-solid fa-spinner fa-spin fa-sm text-white"></i></div>}
                    
                  </form>
                  <hr className='text-muted' />
                  <div className='text-center'>
                    <a className='forgetPass  small  '>ForgetPassword?</a>
                  </div>
                  <div className='text-center'>
                    <span className='small text-muted'>Not a member yet?</span>
                    <Link className='small  creatAccount ' to='/Register' >Create Account
                      <i className='fas fa-chevron-right small'></i>
                    </Link>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </>
}

