import React , { useEffect, useState }from 'react'
import './ActionRpg.css'
import axios from 'axios';
import { Link } from 'react-router-dom';
export default function ActionRpg() {
    const options = {
        method: 'GET',
        url: 'https://free-to-play-games-database.p.rapidapi.com/api/games',
        params: {category: 'action-rpg'},
        headers: {
          'X-RapidAPI-Key': '6902c2dcd3mshc20e7433015f861p191c9bjsn61070e649f14',
          'X-RapidAPI-Host': 'free-to-play-games-database.p.rapidapi.com'
        }
      };
      let [ActionRpg,setActionRpg]=useState()
      let [visibale, setVisibale]=useState(20);
    
      let showMore= () =>{
          setVisibale((prevValue)=>prevValue + 20);
      };
      useEffect(()=>{
        axios.request(options).then(function (response) {
            // console.log(response.data);
            setActionRpg(response.data);
        }).catch(function (error) {
            console.error(error);
        });
      },[])

  return<>
  <section >
            <div className="container my-5">
                
                 <div className="row mt-3 g-3">
                    {ActionRpg && ActionRpg.slice(0,visibale).map((ActionRpg) => {
                        return <div className="col-md-3" key={ActionRpg.id}>
                            
                            <div className="game  card mb-4 grow shadow">
                                <Link to={"/Details/" +ActionRpg.id}>
                                <div className="gameImg">
                                    <img src={ActionRpg.thumbnail} className='w-100' />
                                </div>
                                <div className="game-Body  p-3">
                                    <div className='d-flex justify-content-between align-items-center'>
                                    <h4 className='card-title text-truncate'>{ActionRpg.title}</h4>
                                    <span className='badge badge-ftg py-2 px-2 float-start'>Free</span>  
                                    </div>
                                   
                                    <p className='text-muted'>{ActionRpg.short_description.slice(0,25)}...</p>
                                    <div className='d-flex justify-content-between'>
                                        <i className='fas fa-plus-square'></i>
                                        <div className='d-flex mb-2 justify-content-between align-items-center'>
                                            <span className='badge badge-secondary text-dark me-2 badge-genre'>{ActionRpg.genre}</span>
                                            <i className='fab fa-windows text-muted stretched-link'></i>
                                        </div>
                                    </div>
                                </div>
                                </Link> 
                            </div>
                        </div>
                    })}
                      <button onClick={showMore} className='btn btn-outline-secondary py-2 pt-1 more-btn'>More Games
                        <i className='fas fa-angle-right'></i>
                    </button>
                </div>
                
               
            </div>
        </section>
  </>
}
