import React, { useEffect, useState } from 'react'
import './Details.css'
import OwlCarousel from 'react-owl-carousel'
import 'owl.carousel/dist/assets/owl.carousel.css';
import 'owl.carousel/dist/assets/owl.theme.default.css';
import 'owl.carousel/dist/assets/owl.carousel.css';
import 'owl.carousel';

import { useParams } from 'react-router-dom';
import axios from 'axios';
import $ from 'jquery'

export default function Details() {
    let { id } = useParams()
    const options = {
        method: 'GET',
        url: 'https://free-to-play-games-database.p.rapidapi.com/api/game',
        params: { id: id },
        headers: {
            'X-RapidAPI-Key': '6902c2dcd3mshc20e7433015f861p191c9bjsn61070e649f14',
            'X-RapidAPI-Host': 'free-to-play-games-database.p.rapidapi.com'
        }
    }; 
    let [gameDetaile, setGameDetaile] = useState()
      
    useEffect(() => {
        axios.request(options).then(function (response) {
            // console.log(response.data);
            setGameDetaile(response.data)
           
        }).catch(function (error) {
            console.error(error);
           
        });

    }, [])
    
    return <>


        <section className='detaile' style={{ backgroundImage: `url(${gameDetaile?.thumbnail})` }}>
            <div className="layer ">
                <div className="container">
                    <div className="row pt-5 ">
                        <div className="col-md-4">
                            <div>
                                <div className="media position-relative mb-3">
                                    <video autoPlay className='w-100 top-0 rounded-2 position-relative'>
                                        <source src={gameDetaile?.thumbnail} />
                                    </video>
                                    <img src={gameDetaile?.thumbnail} className='w-100 rounded-2 position-absolute top-0 game-thumbnail' />
                                </div>
                                <div className="row w-100 justify-content-between me-0 pe-0">
                                    <div className="col-3 col-lg-2 me-2">
                                        <span className='btn btn-dark2 mb-3 py-2 '>Free</span>
                                    </div>
                                    <div className="col me-0 pe-0">
                                        <a href={gameDetaile?.freetogame_profile_url} className='btn btn-ftg-blue btn-block w-100 py-2 me-0 ' target='-blank' >
                                            <strong>PLAY NOW </strong>
                                            <i className='fas fa-sign-out-alt'></i>
                                        </a>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="col-md-8">
                            <h1>{gameDetaile?.title}</h1>
                            <h5 className='mt-3'>About {gameDetaile?.title}</h5>
                            <p className='description'>{gameDetaile?.description}</p>
                            {gameDetaile?.minimum_system_requirements ? <>
                                <h5 className='mt-3'>Minimum System Requirements</h5>
                                <ul className="list-unstyled ms-2">
                                    <li className="py-2">
                                        <strong >graphics : </strong> {gameDetaile?.minimum_system_requirements.graphics}

                                    </li>
                                    <li className="py-2">
                                        <strong >memory : </strong>{gameDetaile?.minimum_system_requirements.memory}

                                    </li>
                                    <li className="py-2">
                                        <strong >os :  </strong>{gameDetaile?.minimum_system_requirements.os}

                                    </li>
                                    <li className="py-2">
                                        <strong >processor :</strong>{gameDetaile?.minimum_system_requirements.processor}

                                    </li>
                                    <li className="py-2">
                                        <strong >storage :</strong>{gameDetaile?.minimum_system_requirements.storage}

                                    </li>
                                </ul>
                            </> : ""}

                            {gameDetaile?.screenshots? <><h4 className='pt-3'>{gameDetaile?.title} screenshots</h4>
                                <OwlCarousel className='owl-theme' loop items={1}>
                                    <img className='w-100' height={400} src={gameDetaile?.screenshots[0].image }/>
                                    <img className='w-100' height={400} src={gameDetaile?.screenshots[1].image }/>
                                    <img className='w-100' height={400} src={gameDetaile?.screenshots[2].image }/>
                                    
                                </OwlCarousel>
                            </> : ""}

                            <h2>Additional Information</h2>
                            <hr className='mt-2 mb-3' />
                            <div className="row mb-3">
                                <div className="col-6 col-md-4">
                                    <span className='text-muted'>Title
                                        <br />
                                    </span>
                                    <p>{gameDetaile?.title}</p>
                                </div>
                                <div className="col-6 col-md-4">
                                    <span className='text-muted'>Developer
                                        <br />
                                    </span>
                                    <p>{gameDetaile?.developer}</p>
                                </div>
                                <div className="col-6 col-md-4">
                                    <span className='text-muted'>Publisher
                                        <br />
                                    </span>
                                    <p>{gameDetaile?.publisher}</p>
                                </div>
                                <div className="col-6 col-md-4">
                                    <span className='text-muted'>Release Date
                                        <br />
                                    </span>
                                    <p>{gameDetaile?.release_date}</p>
                                </div>
                                <div className="col-6 col-md-4">
                                    <span className='text-muted'>Genre
                                        <br />
                                    </span>
                                    <p>{gameDetaile?.genre}</p>
                                </div>
                                <div className="col-6 col-md-4">
                                    <span className='text-muted'>Platform
                                        <br />
                                    </span>
                                    <p>{gameDetaile?.platform}</p>
                                </div>
                            </div>
                        </div>
                    </div>

                </div>
            </div>
        </section>
    </>
}
