import React, { useEffect, useState } from 'react'
import Layout from './component/Layout/Layout'
import Login from './component/Login/Login'
import Register from './component/Register/Register'
import Home from './component/Home/Home'
import { Navigate, RouterProvider, createBrowserRouter, createHashRouter } from 'react-router-dom'
import jwt_Decode from 'jwt-decode'
import All from './component/All/All'
import Pc from './component/pc/Pc'
import Browser from './component/browser/Browser'
import Details from './component/Details/Details'
import SortReleseDate from './component/Sort-Relese-date/SortReleseDate'
import Popularity from './component/Popularity/Popularity'
import Alphabetical from './component/alphabetical/Alphabetical'
import Relevance from './component/relevance/Relevance'
import Racing from './component/categories/Racing/Racing'
import Sports from './component/categories/Sports/Sports'
import Social from './component/categories/Social/Social'
import Shooter from './component/categories/Shooter/Shooter'
import OpenWorld from './component/categories/OpenWorld/OpenWorld'
import Zombie from './component/categories/Zombie/Zombie'
import Fantasy from './component/categories/Fantasy/Fantasy'
import ActionRpg from './component/categories/ActionRpg/ActionRpg'
import Action from './component/categories/Action/Action'
import Flight from './component/categories/Flight/Flight'
import BattleRoyale from './component/categories/BattleRoyale/BattleRoyale'
import NotFound from './component/NotFound/NotFound'



export default function App() {
  useEffect(()=>{
    if(localStorage.getItem("token")!==null){
      saveUserData()
    }
  },[])

 let [userData , setUsrData] = useState(null)
 useEffect (()=>{
  if(localStorage.getItem("token")){
    let data = jwt_Decode(localStorage.getItem("token"))
    setUsrData(data)
  }
 },[])
 function saveUserData(data){
  setUsrData(data)
 }

 function logOut(){
  localStorage.removeItem("token")
  setUsrData(null)
  return <Navigate to='/login'/>
 }

function ProtectRouter(props){
  if(localStorage.getItem("token")){
    return props.children
  }
  else{
    return <Navigate to='/login' />
    
  }
}

  let routes = createHashRouter ([
    {path:"/",element: <Layout  setUsrData={setUsrData}  userData={userData} logOut={logOut}/> ,children:[
      {path:"login" , element: <Login saveUserData={saveUserData}/>},
      {index:true, element: <Register  saveUserData={saveUserData}/>},
      {path:"Register", element: <Register  saveUserData={saveUserData}/>},
      {path:"home",element:<ProtectRouter><Home/></ProtectRouter> },
      {path:"all",element:<ProtectRouter><All/></ProtectRouter> },
      {path:"browser",element:<ProtectRouter><Browser/></ProtectRouter>},
      {path:"pc",element:<ProtectRouter><Pc/></ProtectRouter>},
      {path:"Details/:id",element:<ProtectRouter><Details/></ProtectRouter>},
      {path:"sortRelaseDate",element:<ProtectRouter><SortReleseDate/></ProtectRouter>},
      {path:"popularity",element:<ProtectRouter><Popularity/></ProtectRouter>},
      {path:"alphabetical",element:<ProtectRouter><Alphabetical/></ProtectRouter>},
      {path:"relevance",element:<ProtectRouter><Relevance/></ProtectRouter>},
      {path:"racing",element:<ProtectRouter><Racing/></ProtectRouter>},
      {path:"sports",element:<ProtectRouter><Sports/></ProtectRouter>},
      {path:"social",element:<ProtectRouter><Social/></ProtectRouter>},
      {path:"shooter",element:<ProtectRouter><Shooter/></ProtectRouter>},
      {path:"OpenWorld",element:<ProtectRouter><OpenWorld/></ProtectRouter>},
      {path:"zombie",element:<ProtectRouter><Zombie/></ProtectRouter>},
      {path:"fantasy",element:<ProtectRouter><Fantasy/></ProtectRouter>},
      {path:"ActionRpg",element:<ProtectRouter><ActionRpg/></ProtectRouter>},
      {path:"Action",element:<ProtectRouter><Action/></ProtectRouter>},
      {path:"Flight",element:<ProtectRouter><Flight/></ProtectRouter>},
      {path:"BattleRoyale",element:<ProtectRouter><BattleRoyale/></ProtectRouter>},
      {path:"*",element:<NotFound/>},
    ]}
  ])





  return <> 
  <RouterProvider router={routes}/>
  </>

   

}

