import React, { useState } from 'react'
import './Register.css'
import { useFormik } from 'formik'
import * as Yup from 'yup'
import axios from 'axios'
import { Link, useNavigate } from 'react-router-dom'
export default function Register({saveUserData}) {

  let baseUrl = 'https://ecommerce.routemisr.com'
  let [erroMassege, setErrorMassege] = useState("")
  let [loading,setLoading]=useState(true)
  let valid = Yup.object({
    name:Yup.string().required().min(3,"min char 3").max(10,"max char 10"),
    email:Yup.string().email("Enter valid email").required(),
    phone:Yup.string().matches(/^(010|011|015|012)[0-9]{8}$/,"Enter valid phone").required(),
    password:Yup.string().matches(/^[A-Z][a-z0-9!@#$$%^&*()_-]{3,16}$/,"Enter valid password").required(),
    rePassword:Yup.string().required().oneOf([Yup.ref("password")],"valid repassword")
  })

  let formik = useFormik({
    initialValues: {
      name:"",
      email:"",
      phone:"",
      password:"",
      rePassword:""
    },
    onSubmit: (values) => {
      sendRegisterData(values)
    },
    validationSchema: valid
    
  })

  let navigate = useNavigate()

 async function sendRegisterData(values){
   setLoading(false)
    let {data} = await axios.post(`${baseUrl}/api/v1/auth/signup`, values).catch((err)=>{
      setErrorMassege(err.response.data.errors.msg)
      // console.log(data);
      setLoading(true)
    })
    setLoading(true)
    // console.log(data);
    if(data.message=="success"){
      saveUserData(data.user)
      localStorage.setItem("token",data.token)
      navigate('/login')
    }
  }

 


  return <>
    <div className="main1">
      <div className="container p-lg-5">
        <div className="Reg card border-0 shadow-lg  ">
          <div className="card-body p-0">
            <div className="row">
              <div className="col-lg-6 d-none d-lg-block leftPart1"></div>
              <div className="col-lg-6 reg">
                <div className="py-5 px-2">
                  <div className='text-center'>
                    <h1 className='h4 text-muted mb-4'>Create My Account!</h1>
                  </div>
                  <form className='userReg' onSubmit={formik.handleSubmit} >
                    <div className="form-group">
                        <input type='text' onBlur={formik.handleBlur} onChange={formik.handleChange} name='name' placeholder='Name' className='form-control  userFormReg' />
                        {formik.touched.name && formik.errors.name ? <p className='alert alert-warning py-0 mt-1 small'>{formik.errors.name}</p> : ""}
                      </div>
                    <div className='form-group'>
                      <input type='text' onBlur={formik.handleBlur} onChange={formik.handleChange} name='email' placeholder='Email' className='form-control  userFormReg' />
                      {formik.touched.email && formik.errors.email ? <p className='alert alert-warning py-0 mt-1 small'>{formik.errors.email}</p> : ""}
                    </div> 
                    <div className='form-group'>
                      <input type='password' onBlur={formik.handleBlur} onChange={formik.handleChange} name='password' placeholder='Password' className='form-control  userFormReg' />
                      {formik.touched.password && formik.errors.password ? <p className='alert alert-warning py-0 mt-1 small'>{formik.errors.password}</p> : ""}
                    </div>
                    <div className='form-group'>
                      <input type='password' onBlur={formik.handleBlur} onChange={formik.handleChange} name='rePassword' placeholder='rePassword' className='form-control  userFormReg' />
                      {formik.touched.rePassword && formik.errors.rePassword ? <p className='alert alert-warning py-0 mt-1 small'>{formik.errors.rePassword}</p> : ""}
                    </div>
                    <div className='form-group'>
                      <input type='text' onBlur={formik.handleBlur} onChange={formik.handleChange} name='phone' placeholder='phone' className='form-control  userFormReg' />
                      {formik.touched.phone && formik.errors.phone ? <p className='alert alert-warning py-0 mt-1 small'>{formik.errors.phone}</p> : ""}
                    </div>
                   
                    {erroMassege != ""? <div className='alert alert-danger'>{erroMassege}</div> :""}
                    {loading? <button type='submit' name='submitButton' className='btn btn-primary w-100 py-2'>
                      <span>Create Acount</span>
                    </button> : <div className='text-center'><i className="fa-solid fa-spinner fa-spin fa-sm text-white"></i></div>}
                   
                  </form>
                  <div className='text-center mt-2'>
                    <div className='text-muted small pa '>
                      This site is protected by reCAPTCHA and the Google <a href='https://policies.google.com/privacy' className='text-secondary'>Privacy Policy</a>and
                      <a href='https://policies.google.com/terms' className='text-secondary'>Terms of Service</a>apply.
                    </div>
                  </div>
                  <hr className='text-muted' />
                  <div className='text-center'>
                    <span className='small'>Already a member?</span>
                    <Link className='small logIn ms-2 ' to='/login'>Log In <i className='fas fa-chevron-right small'></i></Link>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </>
}
