import React from 'react'
import './Navbar.css'
import img from '../../image/logo.png'
import { Link } from 'react-router-dom'

export default function Navbar({ userData, logOut }) {
  return <>
    <nav className="navbar navbar-expand-lg navbar-dark shadow-sm fixed-top">
      <div className="container">
        <Link className="navbar-brand me-5" href="#">
          <img src={img} className='image' />
          Game Over</Link>
        <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
          <span className="navbar-toggler-icon"></span>
        </button>

        <div className="collapse navbar-collapse show ms-4" id="navbarSupportedContent">

          {userData ? <ul className="navbar-nav me-auto mb-2 mb-lg-0">
            <><li className="nav-item">
              <Link className="nav-link " aria-current="page" to='home'>Home</Link>
            </li>
              <li className="nav-item">
                <Link className="nav-link" to='all' >All</Link>
              </li>
              <li className="nav-item dropdown">
                <a className="nav-link dropdown-toggle show" href="#" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                  Platforms
                </a>
                <ul className="dropdown-menu show">
                  <li><Link className="dropdown-item" to='pc'>Pc</Link></li>
                  <li><Link className="dropdown-item" to='browser'>Browser</Link></li>
                </ul>
              </li>

              <li className="nav-item dropdown">
                <a className="nav-link dropdown-toggle show" href="#" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                  Sort-by
                </a>
                <ul className="dropdown-menu show">
                  <li><Link className="dropdown-item" to="sortRelaseDate">relese-date</Link></li>
                  <li><Link className="dropdown-item" to="popularity">popularity</Link></li>
                  <li><Link className="dropdown-item" to="alphabetical">alphabetical</Link></li>
                  <li><Link className="dropdown-item" to="relevance">relevance</Link></li>
                </ul>
              </li>

              <li className="nav-item dropdown">
                <a className="nav-link dropdown-toggle show" href="#" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                  Categories
                </a>
                <ul className="dropdown-menu show">
                  <li><Link className="dropdown-item" to="racing">racing</Link></li>
                  <li><Link className="dropdown-item" to="sports">sports</Link></li>
                  <li><Link className="dropdown-item" to="social">social</Link></li>
                  <li><Link className="dropdown-item" to="shooter">shooter</Link></li>
                  <li><Link className="dropdown-item" to="OpenWorld">open-world</Link></li>
                  <li><Link className="dropdown-item" to="zombie">zombie</Link></li>
                  <li><Link className="dropdown-item" to="fantasy">fantasy</Link></li>
                  <li><Link className="dropdown-item" to="ActionRpg">action-rpg</Link></li>
                  <li><Link className="dropdown-item" to="Action">action</Link></li>
                  <li><Link className="dropdown-item" to="Flight">flight</Link></li>
                  <li><Link className="dropdown-item" to="BattleRoyale">battle-royale</Link></li>

                </ul>
              </li>
            </>


          </ul> : ""}



          <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
            {userData ? <li className="nav-item m-2">
              <Link className="btn  button w-100 " onClick={logOut} >Log Out</Link>
            </li> :<>
             <li className="nav-item m-2">
              <Link className="nav-link btn py-2 " to='login'>Login</Link>
            </li>
            <li className="nav-item m-2">
              <Link className="nav-link btn button m-auto w-100" to='Register'>Join free</Link>
            </li>
            </> 
          
            }
            

          </ul>







        </div>



      </div>
    </nav >
  </>
}
