import React , { useEffect, useState }from 'react'
import './Sports.css'
import axios from 'axios';
import { Link } from 'react-router-dom';
export default function Racing() {
    const options = {
        method: 'GET',
        url: 'https://free-to-play-games-database.p.rapidapi.com/api/games',
        params: {category: 'sports'},
        headers: {
          'X-RapidAPI-Key': '6902c2dcd3mshc20e7433015f861p191c9bjsn61070e649f14',
          'X-RapidAPI-Host': 'free-to-play-games-database.p.rapidapi.com'
        }
      };
      let [sports,setSports]=useState()
      
      useEffect(()=>{
        axios.request(options).then(function (response) {
            // console.log(response.data);
            setSports(response.data);
        }).catch(function (error) {
            console.error(error);
        });
      },[])

  return<>
  <section >
            <div className="container my-5">
                
                 <div className="row mt-3 g-3">
                    {sports && sports.map((sports) => {
                        return <div className="col-md-3" key={sports.id}>
                            
                            <div className="game  card mb-4 grow shadow">
                                <Link to={"/Details/" +sports.id}>
                                <div className="gameImg">
                                    <img src={sports.thumbnail} className='w-100' />
                                </div>
                                <div className="game-Body  p-3">
                                    <div className='d-flex justify-content-between align-items-center'>
                                    <h4 className='card-title text-truncate'>{sports.title}</h4>
                                    <span className='badge badge-ftg py-2 px-2 float-start'>Free</span>  
                                    </div>
                                   
                                    <p className='text-muted'>{sports.short_description.slice(0,25)}...</p>
                                    <div className='d-flex justify-content-between'>
                                        <i className='fas fa-plus-square'></i>
                                        <div className='d-flex mb-2 justify-content-between align-items-center'>
                                            <span className='badge badge-secondary text-dark me-2 badge-genre'>{sports.genre}</span>
                                            <i className='fab fa-windows text-muted stretched-link'></i>
                                        </div>
                                    </div>
                                </div>
                                </Link> 
                            </div>
                        </div>
                    })}
                    
                </div>
                
               
            </div>
        </section>
  </>
}
